$mainColor: #5567b1;

$subColor : #9DDBE8;

$gray : #4a4b4d;

$lightgray : #6b6b6b;

$sublightgray : #d4d4d4;

.chat-modal-wrap {
    width: 100vw;
    height: 100vh;
    position: fixed;
    left: 0;
    top: 0;
    background-color: rgba(0, 0, 0, 0.1);
    z-index: 100;
}

.chat-modal {
    width: 48px;
    height: 100%;
    background-color: transparent;
    box-sizing: border-box;
    position: absolute;
}

.chat-wrap-btn {
    // position: fixed;
    // z-index: 100;
    // left:42%;
    // top: 20%;
    margin: 17% auto;
}

.wrap {
    width: 100%;
    height: 100%;
    position: fixed;
    top: 15vh;
}

.wrap1 {
    width: 500px;
    height: 600px;
    margin: auto;
    border: 1px solid $mainColor;
    border-radius: 10px;
    display: flex;
    justify-content: center;
    background-color: white;
}

.container {
    margin: 50px;
}

.modal-close {
    float: right;
    color: $mainColor;
    margin-top: -20px;
    margin-right: -15px;
    font-size: 18px;
    cursor: pointer;
}

.modal-close2 {
    float: right;
    color: $mainColor;
    margin-top: 5px;
    margin-right: 6px;
    font-size: 18px;
    cursor: pointer;
}


.chat-form {
    display: flex;
    flex-direction: column;
    margin-bottom: 40px;
    // border: 1px solid red;
}

.chat-div {
    display: flex;
    align-items: flex-end;
    margin-top: 40px;
    width: 380px;
    font-size: 15px;
    // font-weight: bold;
    // border-bottom: 1px solid $mainColor;
}

.chat-div:nth-child(3) {
    align-items: center;
}

.chat-div>p {
    font-weight: normal;
    color: $gray;
}

.chat-div>label {
    width: 50px;
    margin-left: 10px;
    margin-right: 10px;
}

.textline{
    width: 100%;
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}

.chat-form-text {
    margin: 0 5px;
    margin-left: 10px;
    margin-top: -20px;
    color: $lightgray;
    accent-color: none;
    border: none;
}

.chat-form-btn {
    margin: 0 5px;
    margin-left: 60px;
    margin-top: -20px;
    display: flex;
    justify-content: center;
    border: 1px solid $mainColor;
    color: $mainColor;
    height: 30px;
    width: 340px;
    border-radius: 10px;
    text-align: center;
    font-size: 1.3rem;
    background-color: white;
}

.chat-form-btn:hover {
    cursor: pointer;
}

.member-nick {
    display: none;
    transition: all 0.25s;
    margin-left: 5px;
    position: absolute;
    width: 100px;
    left: 0;
    top: 30px;
    text-align: left;
}

.people-photo-wrap:hover+.member-nick {
    display: block;
    transition: all 0.25s;
}

.chat-div label {
    color: $mainColor;
}

.chat-div input[type="text"],
.chat-div input[type="date"] {
    width: 300px;
    height: 25px;
    border-bottom: 1px solid $mainColor;
    background-color: transparent;

    &:focus {
        outline: none;
    }

    &::placeholder {
        color: $sublightgray;
    }
}

.chat-group-name {
    background-color: transparent;
}

.kakao {
    border: 1px solid $mainColor;
    color: $lightgray;
    border-radius: 10px;
    height: 50px;
    margin-top: 50px;
    margin-bottom: 30px;
}


.kakao-div {
    margin: 17px 5px;
    color: $mainColor;
    cursor: pointer;
    text-align: center;
    font-size: 15px;
}

.last-btn {
    padding: 5px 5px 5px 5px;
    color: $mainColor;
    border: 1px solid $mainColor;
    border-radius: 20px;
    margin-top: 50px;
    width: 100px;
    background-color: white;
    text-align: center;
    cursor: pointer;
}

.memberlist {
    margin-bottom: 0;
}


/// 멤버리스트
.people {
    display: flex;
    width: 300px;
    height: auto;
    flex-wrap: wrap;
}

.people>li {
    display: flex;
    flex-direction: row;
    position: relative;
    margin-bottom: 0;
    // height: 50px;
    align-items: center;
}

.people-photo-wrap {
    border: 1px solid transparent;
    border-radius: 100%;
    width: 30px;
    height: 30px;
    overflow: hidden;
}

.people-photo {
    width: 30px;
    height: 30px;
}

.member-nick {
    font-size: 12px;
    color: $lightgray;
    display: none;
    transition: all 0.25s;
    position: absolute;
    width: 100px;
    left: -2px;
    top: 33px;
    z-index: 105;
    text-align: left;
}

.people-photo-wrap:hover+.member-nick {
    display: block;
    transition: all 0.25s;
}

.chat-div>ul>li {
    font-weight: normal;
    color: $gray;
}